import React , { Component } from 'react'
import SOCIAL_PROFILES from './data/socialProfiles'

class SocialProfile extends Component{
    
    render(){
        const { link, image} = this.props.social
        return(
            <span>
               <a href={link}>  
               <img src={ image } alt='contact me' style={{width: 25, breadth: 25, margin: 10}} />
               </a>
            </span>
        )
    }
}

class SocialProfiles extends Component{

    render(){
        return(
            <div>
                <h2> connect with me </h2>
                <div>
                {
            SOCIAL_PROFILES.map( x => {
                return (                                    // for eacch array object, returnig a social profile component
                <SocialProfile key={x.id} social = {x} />   // passing arr obj as data through props, so made the social props
                )
            })
            }
            </div>
            </div>
            
        )
    }
} 

export default SocialProfiles