import React, { Component } from "react";
import Projects from "./Projects";
import SocialProfiles from "./SocialProfiles";
import profilePic from "./assets/profile.jpeg";

class App extends Component {
  state = { displayBio: false };
  toggleDisplayBio = () => {
    //initialiszed a methid within a component
    this.setState({ displayBio: !this.state.displayBio });
  };

  render() {
    return (
      <div>
        <img src={profilePic} alt="profile-pic" className="profilepic" />
        <h1>HEY THERE!!</h1>
        <p>My Name is Sourish. I am a software developer</p>
        <p>Besides it I like to workout and eat food :D </p>
        {this.state.displayBio ? (
          <div>
            <p>living in New Delhi, and code pretty much every day</p>
            <p>I like JavaScript React and Node</p>
            <p>In my free time I like to watch movies</p>
            <button onClick={this.toggleDisplayBio}>Show Less</button>
          </div>
        ) : (
          <div>
            <button onClick={this.toggleDisplayBio}>Read More</button>
          </div>
        )}
        <hr />
        <Projects />
        <hr />
        <SocialProfiles />
        <hr />
      </div>
    );
  }
}

export default App;
